<template>
    <div class="nodeSettingDialog">
        <el-form :model="form" :rules="rules" label-width="120px" ref="nodeSettingForm" :form="form" :macNoList="macNoList" size="mini">
            <el-row>
                <el-col :span="8">
                    <el-form-item label="物流设备编码" prop="diviceNo">
                        <el-select v-model="form.diviceNo" placeholder="" :disabled="form.id>0">
                          <el-option v-for="(item,index) in diviceNoList" :key="index" :label="item.diviceNo" :value="item.diviceNo"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="节点名" prop="sitId">
                        <el-select
                            v-model="form.sitId"
                            :disabled="form.id>0"
                            filterable
                            remote
                            placeholder="请输入关键词"
                            @change="handlerChange"
                            :remote-method="remoteMethod"
                            :loading="loading">
                                <el-option
                                v-for="(item,index) in nodeList"
                                :key="index"
                                :label="item.siteName"
                                :value="item.sitId">
                                </el-option>
                        </el-select>
                        <!-- <el-select v-model="form.siteId" placeholder="请选择节点名" filterable>
                          <el-option v-for="(item,index) in nodeList" :key="index" :label="item.siteName" :value="item.sitId"></el-option>
                        </el-select> -->
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="节点类型" >
                        {{form.siteTypeName}}
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8">
                    <el-form-item label="生产设备编码" prop="prodMacNo">
                        <el-select v-model="form.prodMacNo" placeholder="请选择生产设备编码" filterable>
                          <el-option v-for="(item,index) in prodMacNoList" :key="index" :label="item.deviceName" :value="item.deviceNo"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="列名" prop="listName">
                        <el-select v-model="form.listName" placeholder="请选择生产设备编码" filterable>
                          <el-option v-for="(item,index) in list" :key="index" :label="item" :value="item"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8">
                    <el-form-item label="设备内置节点编码"  label-width="140px">
                        {{form.macInSiteNo}}
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="层数">
                        {{form.layer}}
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
    </div>
</template>
<script>
import {optionProductionDeviceNo,findSit,deviceGet} from './interface.js'
export default {
    props: ["form", "macNoList"],

    data() {
        /**小数校验（保留两位小数） */
        var vlidateDecimal = (rule, value, callback) => {
            var reg = /(^\d+)|(^(-\d+))\.(\d{2})$/;
            if(value) {
                if(reg.test(value)) {
                    callback();
                }else {
                    callback(new Error('请输入小数(保留两位小数)'));
                }
            }else {
                callback();
            }
            
        }
        return {
            loading:false,
            list:["A列","B列"],
            nodeList:[],
            prodMacNoList:[],
            diviceNoList:[],
            rules: {
                sitId: [
                    { required: true,type:'number', message: '请输入节点名', trigger: 'blur' }
                ],
                macInSiteNo: [
                    { required: true, message: '请输入内置节点编码', trigger: 'blur' }
                ],
                diviceNo: [
                    { required: true, message: '请输入物流设备编码', trigger: 'change' }
                ],
                listName: [
                    { required: true, message: '请选择列名', trigger: 'change' }
                ],
                prodMacNo: [
                    { required: true, message: '请选择生产设备编码', trigger: 'change' }
                ],
            }
        }
    },
    methods: {
        remoteMethod(val){
            console.log(val)
            this.loading=true
            findSit({siteName:val}).then(res=>{
                console.log(res,'res')
                this.loading=false
                this.nodeList=res
            })
        },
        handlerChange(val){
            var index=-1
            if(val){
                index=this.nodeList.findIndex(data=>data.sitId==val)
            }
            
            if(index>=0){
                console.log(this.nodeList[index])
                this.form.siteTypeName=this.nodeList[index].siteTypeName
                this.form.macInSiteNo=this.nodeList[index].macInSiteNo
                this.form.layer=this.nodeList[index].layer
                this.form.siteType=this.nodeList[index].siteType
            }else{
                this.form.siteTypeName=''
                this.form.macInSiteNo=''
                this.form.layer=''
                this.form.siteType=''
            }
           
        }
    },
    mounted(){
        optionProductionDeviceNo().then(res=>{
            this.prodMacNoList=res
        })
        deviceGet().then(res=>{
            this.diviceNoList=res
        })
    }
}
</script>
<style lang="less">
    .nodeSettingDialog {
        .el-input-number.is-without-controls .el-input__inner {
            width: 150%;
        }
    }
</style>



