import http from '../../api/base'
const ports = window.sessionStorage.getItem('ports')


export let postWay = (data,Vue,url)=>{
    return http.post(ports+url,data,Vue)
}

export let getWay = (data,Vue,url)=>{
    return http.get(ports+url,data,Vue)
}

export let postJsonWay = (data,Vue,url)=>{
    return http.postJson(ports+url,data,Vue)
}
export let optionProductionDeviceNo = ()=>{
    return http.get('sitSet/optionProductionDeviceNo',{})
}
export let findSit = (data)=>{
    return http.post('sitSet/findSit',data)
}
export let deviceGet = ()=>{
    return http.get('deviceDefine/deviceGet',{})
}



